.app-header {
    height: 70px;
    // display: inline-block;
    .app-logo {
        cursor: pointer;
        img {
            width: rem(80px);
        }
    }

    ul {
        display: flex;
        justify-content: space-around;
        height: 100%;
        align-items: center;
        background-color: rgba($color: white, $alpha: 0.8);
    }
    li {
        margin-inline-end: rem(50px);
    }

    .active-nav {
        font-weight: bold;
        color: red;
    }

    @include for-mobile-layout {
        a {
            justify-content: space-between;
        }
        li {
            margin-inline-end: 0;
            padding: 0 rem(5px);
        }
    }
}
