.signup-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    form {
        padding: 10px;
        display: flex;
        flex-direction: column;
        width: 250px;
        margin-block-end: 20px;
        input {
            border-radius: 10px;
            padding: 10px;
        }
        button {
            border-radius: 10px;
            padding: 10px;
            background-color: #7dd100;
            margin-block-start: 10px;
        }
    }
}
